.home-container {
    width: 100%;
    height: 100%;
    display: flex;
    margin: 0;
    padding: 0;
    align-items: center;
    justify-content: center;
    position: relative;
    flex-direction: column;
    background-color: black;
}
.home-container .title-container{
    position: absolute;
    top: 5%;
    left: auto;
}
.home-container .title {
    border-bottom: 1px solid white;
    color: white;
}
.home-container .subtitle{
    color: white;
    left: auto;
}
.home-container .btn {   
    position: absolute;
    top: 74%;
    left: auto;
}

.wallpaperdesktop {
    width: auto;
    height: 100vh;
}
.wallpaperphone {
    width: auto;
    height: 100vh;
}


@media (max-width: 768px) {
    .wallpaperdesktop {
        display: none;
    }
    .home-container .btn  {
        top: 80%;
    }
}

@media (min-width: 768px) {
    .wallpaperphone {
        display: none;

    }
}